/* Global Styles */

html,body {
  background:#e9e9e9;
  color:#333;
  font-family:"Work Sans", sans-serif;
  line-height:1.4;
  height: 100%;
  width: 100%;
  text-align: center;
}

.App-logo {
  height: 4rem;
  pointer-events: none;
}

.FirstClass {
  color: #bbb;
}

.General {
  width: 50%;
  margin: 0 auto;
}

.General2 {
  width: 60%;
  margin: 0 auto;
}

.TopTab1 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: left;
  align-items: center;
}

.TopTab2 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: left;
  align-items: center;
  font-size: 0.9em;
}

.TopTab1 *,
.TopTab2 * {
  margin: 0 10px 0 10px;
}

.SelectedBlue {
  font-size: 1.1em;
  font-weight:700;
  text-decoration: none;
  color: #16c;
}

.JustBlue {
  font-size: 1.1em;
  text-decoration: none;
  color: #38e;
}

.SmallGrey {
  font-size: 0.9em;
  color: #999;
}

.newsLine {}

.NewsLineItem {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 0 5px 0;
  justify-content: left;
  align-items: center;
}

.NewsLineItemIcon {
  height: 20px;
  width: 20px;
}

.NewsLineItemText {
  margin-left: 20px;
  font-size: 0.8em;
  text-align: left;
  text-decoration: none;
  color: #000;
}
.CurrencyRatesBlock {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 0 5px 0;
  justify-content: left;
  align-items: center;
}

.CurrencyRate {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 10px 5px 0;
  justify-content: left;
  align-items: center;
}

.Currency-symbol {
  margin-left: 5px;
  font-size: 0.9em;
}

.Currency-rate {
  margin-left: 5px;
  font-size: 0.9em;
}

.Currency-trend {
  margin-left: 5px;
  font-size: 0.9em;
  color: #bbb;
}

.Currency-rate-micro {
  margin-left: 5px;
  font-size: 0.8em;
}

.Currency-trend-micro {
  margin-left: 5px;
  font-size: 0.8em;
  color: #bbb;
}

.newsLineContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 10px 5px 0;
  justify-content: left;
  align-items: center;
}

.newsLineMain {}

.newsLineAside {
  font-size: 0.9em;
  margin-left: 50px;
}

.newsLineAsideImg {
  /* height: 100px; */
  width: 150px;
}

.SearchBlock {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 10px 5px 0;
  justify-content: left;
  align-items: center;
}

.Search-form {
  width: 100%;
}

.Search-input {
  width: 80%;
  margin-right: 5px;
}

.gen-banner {
  width: 100%;
  height: 150px;
  margin-top: 20px;
}

.Basement-columns {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 10px 5px 0;
  justify-content:space-around;
  align-items:flex-start
}

.Basement-column {
  min-width: 30%;
  display: block;
}

.Basement-column-header {
  font-size: 1.0em;
  text-align: left;
  text-decoration: none;
  color: #38e;
} 

.Basement-column-block {
  margin-top: 10px;
  margin-bottom: 20px;
}

.Basement-column-content {
  display: flex;
  flex-direction: row;
  flex-wrap:nowrap;
  justify-content: flex-start;
  align-items:center;
  text-decoration: none;
  color: #000;
  margin-bottom: 5px;
}

.Basement-column-content * {
  margin-right: 5px;
}

.Basement-column-bold-text {
  font-size: 0.9em;
  font-weight: 700;
  text-decoration: none;
}

.Basement-column-reg-text {
  font-size: 0.9em;
  font-weight: 200;
  text-decoration: none;
}

.weather {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5px 10px 5px 0;
  justify-content:space-around;
  align-items:flex-start
}

.weather-icon {
  width: 40px;
  height: 30px;
}

.weather-giant-text {
  font-size: 2em;
  font-weight: 600;
}

.weather-light-text {
  font-size: 0.9em;
  font-weight: 400;
  width: 100px;
}

.play-icon {
  height: 15px;
  width: 15px;
}



